import clickCircle from '../../components/zwy/clickCircle.vue';
export default {
	components: {

		clickCircle
	},
	data() {
		return {
			modalName: null,
			AllUserTime: [{
				"time": ""
			}],
			userInfo: {}
		}
	},

	onLoad() {

	},
	onShow() {
		this.isLogin()
		if (this.userInfo.uId != null) {
			this.getAllUserTime()
		}else if (this.userInfo &&this.userInfo.realNameInfo==null){
			uni.showModal({
				title: "请先实名",
				showCancel: false,
				success: function(res) {
					if (res.confirm) {
						console.log("确定了啊")
						uni.switchTab({
							url: "/pages/profile/profile"
						})
					}

				}
			})
		}

	},
	filters: {
		TimeToString: function(value) {
			var hour = Math.floor(value / 60)
			var minute = value % 60
			if (hour < 10) {
				hour = "0" + hour + ":"
			} else {
				hour = hour + ":"
			}
			if (minute < 10) {
				minute = "0" + minute
			}

			return hour + minute
		}
	},
	methods: {
		conClick(e) {
			this.$refs.clickCircle.conClick(e);
		},
		// 判断登录
		isLogin() {
			this.userInfo = global.isLogin();
			console.log(this.userInfo)
			if (!this.userInfo) {
				uni.showModal({
					title: "请先登录",
					showCancel: false,
					success: function(res) {
						if (res.confirm) {
							console.log("确定了啊")
							uni.switchTab({
								url: "/pages/profile/profile"
							})
						}

					}
				})
			}
		},
		getAllUserTime() {
			var that = this;
			uni.request({
				url: "http://47.100.59.153:8080/attendance/time/getAllUserTime",
				success(res) {
					console.log(res.data)
					that.AllUserTime = res.data.allUserTime
					console.log(that.AllUserTime)
				}
			})
		},
		// ListTouch触摸开始
		ListTouchStart(e) {
			this.listTouchStart = e.touches[0].pageX
		},

		// ListTouch计算方向
		ListTouchMove(e) {
			this.listTouchDirection = e.touches[0].pageX - this.listTouchStart > 0 ? 'right' : 'left'
		},

		// ListTouch计算滚动
		ListTouchEnd(e) {
			if (this.listTouchDirection == 'left') {
				this.modalName = e.currentTarget.dataset.target
			} else {
				this.modalName = null
			}
			this.listTouchDirection = null
		}
	},

}
